<template>
  <div class="rank">
      <!-- 排行榜 -->
      <ul class="rankTab">
        <li
          v-for="item in routers"
          :key="item.id"
          @click="toUrl(item.path)"
          :class="{ active:$route.path.indexOf(item.path) != -1 }"
        >
          {{ item.name }}
        </li>
      </ul>
      <router-view></router-view>
  </div>
</template>

<script>
 export default {
 	data() {
 		return {
 			routers: [
 				{ name: "动画", path: "/douga", id: 6 },
 				{ name: "音乐", path: "/music", id: 7 },
 				{ name: "舞蹈", path: "/dance", id: 8 },
 			],
 		};
 	},
 	methods: {
 		toUrl(url) {
 			this.$router.replace('/routerdemo/rank'+url).catch(() => {});
 		},
 	},
 };
</script>

<style scoped>
.rankTab{
	padding-bottom: 18px;
	display: flex;
	align-items: center;
  border:1px solid #fff;
}
.rankTab li {
	cursor: pointer; border:1px solid #fff;
	font-size: 14px;
	padding: 3px 8px;
}
.rankTab li.active{
	background: #00a1d6;
	color: #fff;
	border-radius: 12px; border:1px solid #fff;
}
</style>
